/*wheel*/
@keyframes wheelRotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
@keyframes ballRotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-359deg);
  }
}

.wheel {
  height: 312px;
  width: 312px;
  position: relative;
  left: -75px;
}

.ballTrack{
  width: 212px;
